import React from 'react';

// 
function callEmergency() {
    window.location.href = "tel:112";
}

// - mp3 
function warnSound(){
    let sound = document.getElementById("alertSound");
    sound.play();
}

const CategoryList = () => {
        return(
            <section id="category">
                <div className="category-container">
                    <div className="category-list">
                        <button className="category-item" id="culocation"></button>
                        <button className="category-item" id="safeplace"></button>
                        <button className="category-item" id="urgenttext" onClick={callEmergency}></button>
                        <button className="category-item" id="warning" onClick={warnSound}></button>
                        <audio id="alertSound" src="alert.mp3" preload="auto"></audio>
                    </div>
                </div>
            </section>
        );
}

export default CategoryList;